.Player {
    background-color: #E95B51;
    padding: 7px 12px;
    font-family: 'Krub', sans-serif;
    font-size: 29px;
    border-radius: 7px;
    margin-bottom: 7px;

    display: flex;
}

.Player.Add, .Add #PlayerControl button, .Add #PlayerControl {
    background-color: #8ee951;
}

#name {
    flex-grow: 1;
}

#PlayerControl, #PlayerControl button, #PlayerControl span {
    font-family: 'Krub', sans-serif;
    font-size: 27px;
    background-color: #E95B51;
    margin-left: 7px;
    border: 0px;
}

#PlayerControl button {
    cursor: pointer;
}
